* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.LettersList {
  margin: 0px;
  padding: 0.5vw;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2px;
}

.LettersList a {
  text-decoration: none;
  color: inherit;
}

.Letter {
  height: calc(
    (100vw / 3) - 8px
  ); /* 8px is gap=2px*2 + padding 2px each side on list*/
  background-color: #ddd;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.char {
  font-size: 15vw;
  /*
  font-weight: 500; looks terrible/horrible on iPhone
  */
  color: #337;
  background-color: #cba;
  padding: 0 1vw 0.5vw 1vw;
  margin: 0 0 1vw 0;
  border-radius: 1vw;
  text-align: center;
}

.name {
  font-size: 4vw;
  /*
  padding: 1vw 0.5vw 1vw 0.5vw;
  margin: 0 0 0 0;
  */
}

span {
  background-color: whitesmoke;
  /*
  padding: 0.5vw;
  margin: 0vw 0.5vw 0vw 0.5vw;
  */
  border-radius: 2.5vmin;
}

/* DETAILED VIEW */

.detail {
  display: flex;
  justify-content: center;
  padding: 0.5vmin;
}

.detail__letter {
  display: grid;
  /* grid-template-columns: repeat(6, 1fr); */
  /* grid-template-rows: max-content; */
  grid-template-areas:
    "o t t t t c"
    "ap s s s s an"
    "n n n n n n";
  justify-content: space-between;
  /*
  border: 1px solid black;
  */
  border-radius: 5vmin;
  background-color: #ddd;
  height: 99vmin;
  width: 99vmin;
  gap: 0.5vmin;
  padding: 0.5vmin;
}

.detail__order {
  grid-area: o;
  border-radius: 5vmin;
  /*
  background-color: whitesmoke;
  */
  width: 10vmin;
  height: 10vmin;
  display: flex;
  align-items: center;
  justify-content: center;
}

.detail__order p {
  font-size: 4vmin;
}

.detail__transcription {
  grid-area: t;
  border-radius: 5vmin;
  /*
  background-color: whitesmoke;
  */
  height: 10vmin;
  display: flex;
  align-items: center;
  justify-content: center;
}

.detail__transcription p {
  font-size: 4vmin;
  background-color: whitesmoke;
  border-radius: 5vmin;
  padding: 0.25vmin 2.5vmin;
}

.detail__close {
  grid-area: c;
  border-radius: 5vmin;
  /*
  background-color: whitesmoke;
  */
  width: 10vmin;
  height: 10vmin;
  display: flex;
  align-items: center;
  justify-content: center;
}

.detail__close p {
  font-size: 4vmin;
}

.detail__close p a {
  text-decoration: none;
}

.detail__prev {
  grid-area: ap;
}

.detail__next {
  grid-area: an;
}

.detail__prev,
.detail__next {
  display: flex;
  align-self: center;
  align-items: center;
  justify-content: center;
  width: 10vmin;
  height: 10vmin;
  border-radius: 5vmin;
  /* background-color: whitesmoke; */
}

.detail__prev a,
.detail__next a {
  height: fit-content;
  text-decoration: none;
}

.detail__char {
  grid-area: s;
  color: #337;
  background-color: #cba;
  height: 77vmin;
  width: auto;
  border-radius: 5vmin;
  display: flex;
  justify-content: center;
  align-items: center;
}

.detail__char div {
  font-size: 55vmin;
  /*
  background-color: whitesmoke;
  */
}

.vowel {
  color: #733;
}

.detail__name {
  grid-area: n;
  font-size: 4vmin;
  padding: 0;
  margin: 0;
  height: 10vmin;
  /* background-color: whitesmoke; */
  border-radius: 5vmin;
  display: flex;
  align-items: center;
  justify-content: center;
}

.detail__name p {
  font-size: 4vmin;
  background-color: whitesmoke;
  border-radius: 5vmin;
  padding: 0.25vmin 2.5vmin;
}

li {
  display: inline-block;
  padding: 0 1pt;
}

header {
  display: flex;
}
